import React, { useState, useEffect } from 'react';
import Slider from '@mui/material/Slider';
import Stack from '@mui/material/Stack';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';

const Calculator = () => {
  const [pageviews, setPageviews] = useState(0);
  const [impressions, setImpressions] = useState(0);
  const [numAds, setNumAds] = useState(0);
  const [ctr, setCtr] = useState(0);
  const [clicks, setClicks] = useState(0);
  const [cpc, setCpc] = useState(0);
  const [estimatedRevenue, setEstimatedRevenue] = useState(0);

  const handleSliderChange = (event, newValue, setState, isImpressions = false, isNumClicks = false) => {
    console.log(event, newValue, setState);
    setState(newValue);
    if (isImpressions) {
      // Calculate clicks when impressions change
      calculateClicks(newValue);
    }
    if (isImpressions || isNumClicks) {
      calculateRevenue();
    }
  };

  const calculateClicks = (newImpressions) => {
    // Convert CTR to a decimal
    const newClicks = Math.round(newImpressions * (ctr / 100));
    setClicks(newClicks);
    calculateRevenue();
  };

  const calculateRevenue = () => {
    // Constants
    const constantCPM = 3; // Set your constant CPM rate here

    // Perform the revenue calculation
    const calculatedRevenue = (pageviews / 1000) * constantCPM + clicks * cpc;
    setEstimatedRevenue(calculatedRevenue);
  };

  // Update impressions when pageviews or numAds change
  useEffect(() => {
    setImpressions(pageviews * numAds);
    calculateRevenue();
  }, [pageviews, numAds]);

  // Update clicks when impressions or ctr change
  useEffect(() => {
    calculateClicks(impressions);
    calculateRevenue();
  }, [impressions, ctr]);

  useEffect(() => {
    calculateRevenue();
  }, [clicks, cpc]);


  return (
    <div className="calculator" style={{ backgroundColor: '#f2f6fc', padding: '20px' }}>
      <Stack sx={{ height: 300, marginLeft: '50px', marginTop: '20px' }} spacing={3} direction="row">
  {/* Vertical Sliders */}
  <div className="input-section">
          <Slider
            value={pageviews}
            min={0}
            max={100000}
            step={1000}
            orientation="vertical"
            valueLabelDisplay="auto"
            defaultValue={2000}
            onChange={(e, value) => handleSliderChange(e, value, setPageviews)}
          />
          <Paper elevation={3} sx={{ padding: 2, marginTop: 2 }}>
            <label htmlFor="pageviews">Number of PageViews</label>
          </Paper>
        </div>

        {/* Number of Ads Slider */}
        <div className="input-section">
          <Slider
            value={numAds}
            min={0}
            max={10}
            step={1}
            orientation="vertical"
            valueLabelDisplay="auto"
            defaultValue={1}
            onChange={(e, value) => handleSliderChange(e, value, setNumAds, true)}
          />
          <Paper elevation={3} sx={{ padding: 2, marginTop: 2 }}>
            <label htmlFor="numAds">Number of Ads</label>
          </Paper>
        </div>

  {/* Impressions Slider */}
  <div className="input-section">
          <Slider
            value={impressions}
            min={0}
            max={100000}
            step={1000}
            orientation="vertical"
            valueLabelDisplay="auto"
            defaultValue={2000}
            onChange={(e, value) => handleSliderChange(e, value, setImpressions, true)}
          />
          <Paper elevation={3} sx={{ padding: 2, marginTop: 2 }}>
            <label htmlFor="impressions">Number of Impressions</label>
          </Paper>
        </div>

  {/* <div className="input-section" style={{ textAlign: 'center' }}>
    <Slider
      value={numAds}
      min={0}
      max={10}
      step={1}
      orientation="vertical"
      valueLabelDisplay="auto"
      defaultValue={0}
      onChange={(e, value) => handleSliderChange(e, value, setNumAds)}
    />
    <Paper elevation={3} sx={{ padding: 2, marginTop: 2 }}>
      <label htmlFor="num-ads">Number of Ads</label>
    </Paper>
  </div> */}

  <div className="input-section" style={{ textAlign: 'center' }}>
    <Slider
      value={ctr}
      min={0}
      max={10}
      step={0.2}
      orientation="vertical"
      valueLabelDisplay="auto"
      defaultValue={0.2}
      onChange={(e, value) => handleSliderChange(e, value, setCtr)}
    />
    <Paper elevation={3} sx={{ padding: 2, marginTop: 2 }}>
      <label htmlFor="ctr">Click Through Rate (%)</label>
    </Paper>
  </div>

  <div className="input-section" style={{ textAlign: 'center' }}>
    <Slider
      value={clicks}
      min={0}
      max={10000}
      step={20}
      orientation="vertical"
      valueLabelDisplay="auto"
      defaultValue={40}
      onChange={(e, value) => handleSliderChange(e, value, setClicks)}
    />
    <Paper elevation={3} sx={{ padding: 2, marginTop: 2 }}>
      <label htmlFor="clicks">Number of Clicks Per Day</label>
    </Paper>
  </div>

  <div className="input-section" style={{ textAlign: 'center' }}>
    <Slider
      value={cpc}
      min={0}
      max={10}
      step={0.01}
      orientation="vertical"
      valueLabelDisplay="auto"
      defaultValue={0}
      onChange={(e, value) => handleSliderChange(e, value, setCpc)}
    />
    <Paper elevation={3} sx={{ padding: 2, marginTop: 2 }}>
      <label htmlFor="cpc">Cost per Click ($)</label>
    </Paper>
  </div>
</Stack>


      {/* Result Section */}
      <Paper className="margin" elevation={3} style={{ marginLeft: '50px', marginTop: '120px', padding: '20px' }}>
        <Typography variant="h6" gutterBottom>
          Estimated Revenue Per Day
        </Typography>
        <Typography variant="h4">
          ${estimatedRevenue.toFixed(2)}
        </Typography>
      </Paper>
    </div>
  );
};

export default Calculator;
